<template>
    <div>

        <div class="theme-primary partition-full">
            <span class="p-name text-white">Add</span>
        </div>

        <section class="main-content">
            <div class="container-fluid p-0">
                <div class="border p-3">

                    <ValidationObserver v-slot="{ handleSubmit }">
                        <form v-on:submit.prevent="handleSubmit(submit)" ref="form">
                            <div class="row">
                                <div class="col-lg-6">
                                    <div class="form-group">
                                        <ValidationProvider :rules="'required'" v-slot="{ errors }" name="Tag name">
                                            <label class="" for="TagName">{{ $t('TagName') }}</label>:<span
                                                class="mandatory">*</span><span
                                                class="field-validation-valid invalid-feedback"
                                                data-valmsg-for="TagName" data-valmsg-replace="true"></span>
                                            <input type="text" @change="validate" v-model="tagsData.tagName"
                                                class="form-control" :class="errors[0] ? 'is-invalid' : ''"
                                                maxlength="100">
                                            <span class="invalid-feedback">{{ errors[0] }}</span>
                                        </ValidationProvider>
                                    </div>
                                </div>
                                <div class="col-lg-6">
                                    <div class="form-group">
                                        <label class="" for="TagName">{{ $t('status_id') }}</label>:<span
                                            class="mandatory"></span><span
                                            class="field-validation-valid invalid-feedback" data-valmsg-for="TagName"
                                            data-valmsg-replace="true"></span>

                                        <multiselect :placeholder="$t('SelectStatus')"
                                            class="form-group singleselectlook" v-model="tagsData.Status"
                                            :options="tagsOption" label="name" track-by="id" :show-labels="false"
                                            id="exampleFormControlSelect1">
                                            <span slot="noResult">{{ $t('No_Record_Found') }}</span>
                                        </multiselect>

                                    </div>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-lg-12">
                                    <div class="form-group">
                                        <label>{{ $t('TagDescription') }}:</label>

                                        <textarea name="tagDescription" v-model="tagsData.tagDescription"
                                            class="form-control" :placeholder="$t('PleaseEnterDescription')"
                                            maxlength="2000"></textarea>


                                    </div>
                                </div>
                            </div>

                            <div class="row flex-row-reverse w-100">

                                <div class="col-lg-6 text-right p-0">
                                    <button type="submit" class="btn btn-success"><i class="fa fa-save pr-2"></i>{{
                        $t('Save') }}</button>
                                    <button @click="$emit('close', false)" type="button" class="btn btn-danger"
                                        style="margin-left: 5px;"><i class="fa fa-close pr-2"></i>{{ $t('Cancel')
                                        }}</button>

                                </div>


                                <div class="col-lg-6 p-0">
                                    <div class="py-2"><span class="mandatory">{{ $t('MandatoryString') }}</span></div>
                                </div>

                            </div>

                        </form>
                    </ValidationObserver>
                </div>
            </div>
        </section>


        <Alert :type="store.getAlertMessage.type" :message="store.getAlertMessage.message"
            v-bind:open="store.getAlertMessage.open" v-on:close="store.$state.getMessageAlert.open=false" />
    </div>
</template>

<style>
.singleselectlook .multiselect__select::before {
    z-index: 99;
}

.singleselectlook .multiselect__tags {
    padding: 8px 30px 0 8px !important;
    min-height: 36px !important;
}

.singleselectlook .multiselect__input {
    margin-bottom: 0px;
}
</style>

<script>
import { defineComponent, } from '@vue/composition-api'
import { ValidationProvider, extend, ValidationObserver } from 'vee-validate';
import Multiselect from 'vue-multiselect'
import { userTimerInfo } from "../../stores/TimeTrack.js"
import Alert from '../../components/Common/Alert.vue';
extend('required', {
    validate(value) {
        console.log(value)
        if (value.match(/^\s*$/)) {
            value = ''
        }
        return {
            required: true,
            valid: ['', null, undefined].indexOf(value) === -1
        };

    },
    message: 'The {_field_} must be required',
    computesRequired: true
});
export default defineComponent({
    name: "Tags",
    components: {
        Multiselect,
        ValidationProvider,
        ValidationObserver,
        Alert
    },
    data() {
        return {
            tagsOption: [{
                name: "Active",
                id: '1001'
            },
            {
                name: "Inactive",
                id: '1002'
            }

            ],
            tagsData: {
                tagName: '',
                Status: {
                    name: "Active",
                    id: '1001'
                },
                tagDescription: ''
            }
        }

    },
    created: function () {

    },
    methods: {
        async submit() {
            console.log("tags1", this.tagsData.Status)
            this.tagsData.Status = this.tagsData.Status ? this.tagsData.Status.id : ''
            console.log("tags", this.tagsData.Status)
            await this.store.tagsSave(this.tagsData)
            this.store.tagGetForTimer()
            if (this.store.getTagSaveRes == "added Successfully") {
                this.$emit('close', false)
            }
            this.tagsData.Status = {
                name: "Active",
                id: '1001'
            }
        },
        validate() {
            if (this.tagsData.tagName.match(/^\s*$/)) {
                this.tagsData.tagName = ''
            }

        },

    },
    setup() {
        const store = userTimerInfo();
        return {
            store,

        }
    },
});
</script>