<template>
    <div>
        <div class="overlay" id="notifymessage" style="" v-if="open">
            <div class="alert alert-dismissible fade show msg mw-90" :class="typeClass" role="alert">
                <span>{{message  }}</span>
                <button type="button" class="close p-1" id="closenotifymessage" style="background:none !important;" @click="$emit('close');close()">
                    <span aria-hidden="true"><i class="fa fa-times text-secondary"></i></span>
                </button>
            </div>
        </div>
    </div>
</template>
<script>
import {
    defineComponent,
} from '@vue/composition-api'

export default defineComponent({
    components: {

    },
    props: {
        message: {
            type: String,
        },
        type:{
            type:String
        },
        open:{
            type:Boolean
        },
    },
    data() {
        return {
            typeClass:''
        }
    },
    created() {
            this.changeClassName()
    },
    methods: {
        changeClassName(){

            if(this.type = 'error'){
                this.typeClass = 'alert-danger'
            }else if(this.type='warning'){
                this.typeClass = 'alert-warning'
            }else{
                this.typeClass = 'alert-success'
            }
        },
        close(){
            this.open = false
            this.$emit('close')
        }
    }
})
</script>
